.TodoItem {
    background-color: #534374;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px 20px rgba(6, 11, 19, 0.15);
    border-radius: 25px;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p-complete {
    text-decoration: line-through;
}